    //Recorro el map
    for (const [key, listDebt] of listUserToPay) {
      const listDebtNew: Array<Payment> = new Array<Payment>();
      for (const debt of listDebt) {
        if (debt.amount > 0) {
          const userDebt = listDebtNew.find(
            (u) => u.user.name === debt.user.name
          );
          //Sumo los usuarios que se tranfirieron para pagar.
          if (userDebt) {
            userDebt.amount += debt.amount;
          } else {
            listDebtNew.push(debt);
          }
        }
      }
      listUserPay.set(key, listDebtNew);
    }
    cleanMapUserHaveToPay.set(currency, listUserPay);
  }

  return cleanMapUserHaveToPay;
};

const isDebtSolvedWithOwnListUserDebt = (userDebt: Payment, userDebtOfDebt: Payment): boolean => {
  if (Number(userDebt.amount) <= Number(userDebtOfDebt.amount)) {
    userDebtOfDebt.amount -= userDebt.amount;
