const findUserById = (list: Array<Payment>, idUser: string) => {
  // Agrego a la lista el usuario deudor.
  return list?.find((u) => u.user.idUser === idUser);
};

const addUserDebtToArray = (
  listDebt: Payment[],
  userName: string,
  listDebtResult: Payment[] | undefined
): Payment[] => {
  if (!listDebtResult) {
    listDebtResult = new Array<Payment>();
  }
  //Usuarios deudores para el pago que realizó los pagadores
  for (const debt of listDebt) {
    //Excluyo al usuario pagador (a si mismo) de la lista de deudores
    if (debt.user.name !== userName) {
      // Agrego a la lista el usuario deudor.
      const userDebt4UserPaid = findUserById(listDebtResult, debt.user.idUser);

      if (userDebt4UserPaid) {
        userDebt4UserPaid.amount += debt.amount;
      } else {
        listDebtResult.push(debt);
      }
    }
  }
  return listDebtResult;
};

const calculateSummaryTotalHaveToPay4User = (listBill: BillDTO[]) => {
  let listDebt4UserPaid;
  const resultUserPayAux = new Map<string, Map<string, Array<Payment>>>();

  // Recorro lista de pagos, registro usarios pagadores y deudores con sus montos diferenciado por moneda.
  for (const bill of listBill) {
    const currencyCode = bill.receipt.currency.code;

    let listUserDebt4UserPaid = resultUserPayAux.get(currencyCode);
    if (!listUserDebt4UserPaid) {
      listUserDebt4UserPaid = new Map<string, Array<Payment>>();
    }

    //Recorro lista pagadores
    for (const iter of bill.listUsersPaid) {
      //Usuario y monto pagador
      const userName = iter.userPaid.user.name;

      //Agrupo por menedas
      if (listUserDebt4UserPaid) {
        //Obtengo el usuario pagador.
        listDebt4UserPaid = listUserDebt4UserPaid.get(userName);
      }

      //Creo la lista de deudores por usuario.
      //Nota: No se debe agregar a si mismo.
      listDebt4UserPaid = addUserDebtToArray(
        iter.listUsersDebt,
        userName,
        listDebt4UserPaid
      );

      if (listDebt4UserPaid.length > 0) {
        //Agrego a la lista de deudores por pagador, si no existe no agrego una lista vacia
        // NOTA: La clave es el nombre del usuario.
        listUserDebt4UserPaid.set(userName, listDebt4UserPaid);
      }
    }
    if (listUserDebt4UserPaid.size > 0) {
      //Agrego el usuario por Discriminado por moneda
      resultUserPayAux.set(currencyCode, listUserDebt4UserPaid);
    }
  }
  return resultUserPayAux;
};

/**------------------ TOTAL ------------------**/

const addUserToMap = (
  mapCheckTemp: Map<string, Map<string, number>>,
  userName: string,
  totalAmount: number,
  currencyCode: string
) => {
  const listUser = mapCheckTemp.get(userName);

  if (listUser) {
    let amount = listUser.get(currencyCode);
    if (amount) {
      amount += totalAmount;
    } else {
      amount = totalAmount;
    }
    listUser.set(currencyCode, amount);
  } else {
    const payment = new Map<string, number>();
    payment.set(currencyCode, totalAmount);
    mapCheckTemp.set(userName, payment);
  }
  return mapCheckTemp;
};

const calculateTotalPaidAndDebt4User = (listBill: BillDTO[]) => {
  //Recorro lista de pagos y calculo los montos totales de pagos y deudas.
  //Realizo el calculo de cuanto deben cada usuarios.
  mapCheckPaid.value = new Map<string, Map<string, number>>();
  mapCheckDebt.value = new Map<string, Map<string, number>>();

  // Recorro lista de pagos, registro usarios pagadores y deudores con sus montos diferenciado por moneda.
  for (const bill of listBill) {
    const currencyCode = bill.receipt.currency.code;

    //Recorro lista pagadores
    for (const iter of bill.listUsersPaid) {
      const userName = iter.userPaid.user.name;

      const totalAmount = Number(iter.userPaid.amount);
      //Agrego al mapa al usuario pagador y sumo a su total.
      mapCheckPaid.value = addUserToMap(
        mapCheckPaid.value,
        userName,
        totalAmount,
        currencyCode
      );

      //Usuarios deudores para el pago que realizó los pagadores
      for (const debt of iter.listUsersDebt) {
        //Agrego al mapa al usuario deudor y sumo a su total.
        mapCheckDebt.value = addUserToMap(
          mapCheckDebt.value,
          debt.user.name,
          debt.amount,
          currencyCode
        );
      }
    }
  }
};

onIonViewWillEnter(() => {
  showProgress.value = true;

  // Esto es de modo informativo.
  //Calculo monto total por usuario en pagos y deudas
  calculateTotalPaidAndDebt4User(CloneObj(billCtrl.getListBill()));

  //
  resultUserHaveToPay.value = calculateSummaryTotalHaveToPay4User(
    CloneObj(billCtrl.getListBill())
  );

  //Reduzco el resultado para minimizar las transacciones
  resultUserHaveToPay.value = reduceTransactionUserHaveToPay(
    CloneObj(resultUserHaveToPay.value)
  );

  //Elimino los usuarios que saldaron sus deudas.
  resultUserHaveToPay.value = cleanDebtAmountSolved(resultUserHaveToPay.value);

  showProgress.value = false;
});

